import {isBefore, isEmail, isEmpty, isMobilePhone} from "validator";
import React from "react";

const required = (value) => {
  if (isEmpty(value)) {
    return <small className="form-text text-danger">Bn cha in  ny</small>;
  }
}

const phone = (value) => {
  if (!isMobilePhone(value)) {
    return <small className="form-text text-danger">Khng phi s in
      thoi</small>;
  }
}

const email = (value) => {
  if (!isEmail(value)) {
    return <small className="form-text text-danger">nh dng email khng
      ng</small>;
  }
}

const minLength = (value) => {

  if (value > 0 && value.length < 6) {
    return <small className="form-text text-danger">Mt khu phi c ti thiu 6
      k t</small>;
  }
}

const isAfterToday = (value) => {
  let today = new Date().toISOString().slice(0, 10)
  if (isBefore(value, today)) {
    return <small className="form-text text-danger">Ngy khng hp l</small>;
  }
}

export {required, phone, email, minLength, isAfterToday};
